'use client';

import React, { useState, useEffect } from 'react';
import { login, type LoginState } from '@/app/actions';
import { useAuth } from '@/context/auth-context';

import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { AlertCircle, LogIn, User, GraduationCap, Shield } from 'lucide-react';
import { Input } from '@/components/ui/input';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { UserRole } from '@/types';

function SubmitButton({ pending }: { pending: boolean }) {
  return (
    <Button 
      type="submit" 
      disabled={pending} 
      className="w-full bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white font-semibold py-3 px-6 rounded-xl shadow-lg hover:shadow-xl transform hover:scale-[1.02] transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none"
    >
      {pending ? (
        <div className="flex items-center gap-2">
          <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
          Signing In...
        </div>
      ) : (
        <div className="flex items-center gap-2">
          Sign In
          <LogIn className="w-4 h-4" />
        </div>
      )}
    </Button>
  );
}

export function LoginForm() {
  const { login: authLogin } = useAuth();
  const [selectedRole, setSelectedRole] = React.useState<UserRole>('student');
  const [pending, setPending] = useState(false);
  const [state, setState] = useState<LoginState>({ message: null, errors: {} });

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setPending(true);
    
    const formData = new FormData(event.currentTarget);
    const email = formData.get('email') as string;
    const password = formData.get('password') as string;
    
    try {
      // Simple client-side validation and direct login
      if (!email || email.trim() === '') {
        setState({ message: 'Email is required', errors: { email: ['Email is required'] } });
        return;
      }
      
      if (!password || password.trim() === '') {
        setState({ message: 'Password is required', errors: { password: ['Password is required'] } });
        return;
      }
      
      // Mock authentication - accept any non-empty email and password
      setState({ message: 'Login successful!', success: true });
      
      // Mock user data based on selected role
      const roleData = {
        student: {
          name: 'John Student',
          email: email,
          avatarUrl: 'https://picsum.photos/seed/student/40/40'
        },
        faculty: {
          name: 'Dr. Jane Faculty',
          email: email,
          avatarUrl: 'https://picsum.photos/seed/faculty/40/40'
        },
        admin: {
          name: 'Admin User',
          email: email,
          avatarUrl: 'https://picsum.photos/seed/admin/40/40'
        }
      };

      const user = {
        id: `user-${Date.now()}`,
        role: selectedRole,
        ...roleData[selectedRole]
      };
      
      // Login successful - redirect to dashboard
      authLogin(user);
      
    } catch (error) {
      setState({ message: 'Login failed', errors: {} });
    } finally {
      setPending(false);
    }
  };

  useEffect(() => {
    try {
      // Focus on email input when component mounts
      const emailInput = document.querySelector('input[name="email"]') as HTMLInputElement;
      if (emailInput) {
        emailInput.focus();
      }
    } catch (error) {
      // Ignore extension interference errors
      console.log('Extension interference detected, ignoring focus error');
    }
  }, []);

  return (
    <form onSubmit={handleSubmit}>
      <Card className="backdrop-blur-xl bg-white/80 border-white/20 shadow-2xl">
        <CardHeader className="text-center pb-6">
          <CardTitle className="text-2xl font-bold bg-gradient-to-r from-gray-900 to-gray-700 bg-clip-text text-transparent">
            Sign In
          </CardTitle>
          <CardDescription className="text-gray-600">
            Use your credentials to access your account.
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Role Selection */}
          <div className="space-y-4">
            <Label className="text-sm font-semibold text-gray-700">Login As</Label>
            <RadioGroup value={selectedRole} onValueChange={(value) => setSelectedRole(value as UserRole)} className="space-y-3">
              <div className="flex items-center space-x-3 p-3 rounded-xl border border-gray-200/50 hover:bg-blue-50/50 transition-all duration-200 group">
                <RadioGroupItem value="student" id="student" className="border-2" />
                <Label htmlFor="student" className="flex items-center gap-3 cursor-pointer flex-1">
                  <div className="p-2 rounded-lg bg-blue-100 group-hover:bg-blue-200 transition-colors">
                    <GraduationCap className="h-4 w-4 text-blue-600" />
                  </div>
                  <div>
                    <div className="font-medium text-gray-900">Student</div>
                    <div className="text-xs text-gray-500">Access your class schedules</div>
                  </div>
                </Label>
              </div>
              <div className="flex items-center space-x-3 p-3 rounded-xl border border-gray-200/50 hover:bg-purple-50/50 transition-all duration-200 group">
                <RadioGroupItem value="faculty" id="faculty" className="border-2" />
                <Label htmlFor="faculty" className="flex items-center gap-3 cursor-pointer flex-1">
                  <div className="p-2 rounded-lg bg-purple-100 group-hover:bg-purple-200 transition-colors">
                    <User className="h-4 w-4 text-purple-600" />
                  </div>
                  <div>
                    <div className="font-medium text-gray-900">Faculty</div>
                    <div className="text-xs text-gray-500">Manage teaching schedules</div>
                  </div>
                </Label>
              </div>
              <div className="flex items-center space-x-3 p-3 rounded-xl border border-gray-200/50 hover:bg-green-50/50 transition-all duration-200 group">
                <RadioGroupItem value="admin" id="admin" className="border-2" />
                <Label htmlFor="admin" className="flex items-center gap-3 cursor-pointer flex-1">
                  <div className="p-2 rounded-lg bg-green-100 group-hover:bg-green-200 transition-colors">
                    <Shield className="h-4 w-4 text-green-600" />
                  </div>
                  <div>
                    <div className="font-medium text-gray-900">Admin</div>
                    <div className="text-xs text-gray-500">Full system access</div>
                  </div>
                </Label>
              </div>
            </RadioGroup>
          </div>

          {/* Email Input */}
          <div className="space-y-2">
            <Label htmlFor="email" className="text-sm font-semibold text-gray-700">Email</Label>
            <div className="relative">
              <Input 
                id="email"
                name="email"
                type="email"
                placeholder="user@example.com"
                className="pl-4 pr-4 py-3 bg-white/50 border-gray-200/50 focus:bg-white focus:border-blue-400 transition-all duration-200"
              />
            </div>
            {state.errors?.email && (
              <p className="text-sm text-red-500 mt-1 flex items-center gap-1">
                <AlertCircle className="h-3 w-3" />
                {state.errors.email[0]}
              </p>
            )}
          </div>

          {/* Password Input */}
          <div className="space-y-2">
            <Label htmlFor="password" className="text-sm font-semibold text-gray-700">Password</Label>
            <div className="relative">
              <Input 
                id="password"
                name="password"
                type="password"
                placeholder="••••••••"
                className="pl-4 pr-4 py-3 bg-white/50 border-gray-200/50 focus:bg-white focus:border-blue-400 transition-all duration-200"
              />
            </div>
            {state.errors?.password && (
              <p className="text-sm text-red-500 mt-1 flex items-center gap-1">
                <AlertCircle className="h-3 w-3" />
                {state.errors.password[0]}
              </p>
            )}
          </div>

          {/* Error Alert */}
          {state.message && !state.success && (
            <Alert variant="destructive" className="bg-red-50 border-red-200">
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>Login Failed</AlertTitle>
              <AlertDescription>{state.message}</AlertDescription>
            </Alert>
          )}
        </CardContent>
        <CardFooter className="pt-6">
          <SubmitButton pending={pending} />
        </CardFooter>
      </Card>
    </form>
  );
}